import React, {useState} from 'react';
import Textform from './components/Textform';
import Alert from './components/Alert';

// import {
//   BrowserRouter as Router,
//   Routes,
//   Route,
//   Link
// } from "react-router-dom";

function App() {
  const[mode,setMode]= useState('light'); // whether dark mode is enabled or not
  const [alert, setAlert] = useState(null);
  const showAlert =(message, type)=>{
    setAlert({
      msg:message,
      type: type            //Alert is an object
    })
    setTimeout(() => {
      setAlert(null)
    }, 3000);
  }

  const toggleMode=() =>{
if(mode=== 'light'){
  setMode('dark');
  document.body.style.backgroundColor='#151414';
  showAlert("Dark mode has been enabled",`success`)
  document.title='Text-Utils-- Dark Mode'

  // setInterval(() => {
  //   document.title='Text-Utils is amazing'
  // }, 2000);
  // setInterval(() => {
  //   document.title='Text-Utils is installing...'
  // }, 1300);

// above mentioned for scam-y websites, gives prompts at regular intervals

}
else{
  setMode('light');
  document.body.style.backgroundColor='white';
  showAlert("Light mode has been enabled",`success`)
  document.title='Text-Utils-- Light Mode'
}
  }
  return (
    <>
{/* <Router> */}
    <Navbar title="Text-Utils" mode={mode} toggleMode={toggleMode}/>
    <Alert alert={alert}/>
    <div className="container">
      {/* <Routes>
          <Route exact path="/about"
           element={ <About mode={mode}/>}/>
          <Route exact path="/"
          element= {<Textform showAlert={showAlert} head="Enter your text" mode={mode} toggleMode={toggleMode}/>}/>
      </Routes>
      </div>  
</Router> */}
<Textform showAlert={showAlert} head="Enter your text" mode={mode} toggleMode={toggleMode}/>
{/* <About mode={mode}/> */}
</div> 
    </>
  );
}

export default App;
