import React, {useState} from 'react'  //use of hook

export default function Textform(props) {
    const handleUpClick=() =>{
        // console.log("Uppercase was clicked" + text)
        let newText=text.toUpperCase();
        setText(newText)
        props.showAlert("Converted to UpperCase","Success")
    }
    const handleLowClick=() =>{
      // console.log("Uppercase was clicked" + text)
      let newText=text.toLowerCase();
      setText(newText)
      props.showAlert("Converted to LowerCase","Success")
  }
  const handleCopy=()=>{
    console.log("I am copy")
    var text= document.getElementById("mybox")
    text.select();
    text.setSelectionRange(0,9999);
    navigator.clipboard.writeText(text.value);
    props.showAlert("Copied to clipboard", "Success")
  }
    const handleUpChange=(event) =>{
        // console.log("on change")
        setText(event.target.value);
    }
    const [text, setText] = useState(''); //*** 
  return (
  <>
                  <div className={`container text text-${props.mode === 'light'?'dark':'light'}`}>            
                      <h1>{props.head}</h1>
                      <div className="mb-3">
                      {/* <label htmlFor="mybox" className="form-label">{props.head}-{text}</label> */}
                      <textarea className="form-control" value={text} onChange={handleUpChange} id="mybox" rows="8" style= {{backgroundColor:props.mode === 'light'?'white':'rgba(var(--bs-dark-rgb)', color: props.mode === 'light'?'black':'white'}}></textarea> 
                </div> 
                <button className="btn btn-primary my-1" onClick={handleUpClick}>Convert to upper case</button>
                <button className="btn btn-primary mx-1 my-1" onClick={handleLowClick}>Convert to lower case</button>
                <button className="btn btn-primary my-1" onClick={handleCopy}>Copy Text</button>
    </div>
<div className={`container my-4 text-${props.mode === 'light'?'dark':'light'}`}>
  <h1>Text Summary</h1>
  <p>{text.split(" ").filter((element)=>{return element.length!==0}).length} words {text.length} characters</p>
  <p>Read in {0.008*(text.split(" ").filter((element)=>{return element.length!==0}).length)} minutes</p>
</div>
    </>    
  )
}// on change was use to allow us to type and make the changes in already mentioned text
